import React, { Component } from 'react';

const Header = ({ currentUser, listenToAuth, signIn }) => {
  return (
    <section id="signin-screen">
      <button id="login-button" onClick={e => signIn ()}>Sign In </button>
      <h1> {currentUser.email} </h1>
    </section>
  )
}

export default Header



